import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { Http, Headers } from '@angular/http';
import { Slides } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-root-modal',
  templateUrl: 'root-modal.html',
})
export class RootModalPage {
	@ViewChild('slides') slides: Slides;

	concept:string;
	title:string;
	images:Array<any>;
	grid:Array<Array<any>>;
	query:any;
	imageUrl:any;
	step:number;
	totalSteps: number;
	color: string;

	showSpinner: boolean;
  showImagesPanel:boolean;
	noResults:boolean;

	gettyCredentials = {
		apiKey: "5zf3zt29mkfvjecece6kks2g",
		apiSecret: "uwWNXw2u2h77HFgQKxtmZNuFdSSKpcHp5R7zqh6V7FG28",
		username: "andrea.bellucci",
		password: "xxxxxxxxxxxxxxx"
	};

	gettyImagesClient: any;
	colors: Array<string>;


  constructor(public navCtrl: NavController,
		public viewCtrl: ViewController,
		public navParams: NavParams,
		public http:Http) {
			this.images = [];
			this.query = "";
			this.imageUrl = null;
			this.color = "black";
			//this.concept = this.navParams.get('concept');
			//this.query = this.concept;
			this.concept = "";
			this.query = "";
			this.searchImages();
			this.title = "Insert concept";
			this.step = 0;
			this.totalSteps = 4;
			this.colors = ["black", "green", "yellow", "brown",
			"orange", "indigo", "red", "blue",
			"pink", "purple", "cyan", "teal",
			"grey", "blue-grey", "lime", "amber"];
  }

  ionViewDidLoad() {
		this.slides.lockSwipes(true);
	}

	changeTitle( n ){
		switch(n){
			case 0:
				this.title = "Insert concept";
				break;
			/*case 1:
				this.title = "Select an image";
				break;
				*/
			case 1:
				this.title = "Pick a color";
				break;
			case 2:
				this.title = "Last step...";
				break;
		}
	}

	setConcept(){
		this.query = this.concept;
		this.slides.lockSwipes(false);
		this.gotoSlide( 1 );
		this.slides.lockSwipes(true);
		//this.searchImages();
	}

	slideChanged(){
		this.step = this.slides.getActiveIndex();
		if(this.step >= this.totalSteps) this.step = this.totalSteps - 1;
		this.changeTitle(this.step);
	}

	gotoSlide( n ){
		this.slides.slideTo(n, 500);
		this.changeTitle(n);
	}

	setImageUrl( url ){
		this.imageUrl = url;
		this.gotoSlide( 2 );
	}

	dismiss( isDone ){
		if(isDone){
			this.viewCtrl.dismiss(
				{
					concept: this.concept,
					img_url: this.imageUrl,
					color: this.color
				}
			);
		} else {
			this.viewCtrl.dismiss(null);
		}
	}

	onChange( ev ){

		//console.log( this.concept );
	}

	onInput( ){
		if(this.query === ""){
					this.images = [];
					this.showImagesPanel = false;
					return;
		}

		this.searchImages();

	}

	selectImage( ev ){
		//setImage( )
		this.gotoSlide( 2 );
		this.imageUrl = ev.srcElement.currentSrc;
		//ev.srcElement.parentElement.style.border = "5px solid black";
	}

	pickColor( color ){
		this.slides.lockSwipes(false);
		this.gotoSlide( 2 );
		this.slides.lockSwipes(true);

		this.color = color;
	}

  searchImages(  ){
		if(this.query.trim() === "") return;
		this.showSpinner = true;
		this.noResults = false;
		this.showImagesPanel = true;
		this.images = [];
		this.grid = [];

		let headers = new Headers();
		headers.append('Api-Key', this.gettyCredentials.apiKey);
		this.http.get('https://api.gettyimages.com/v3/search/images?phrase='+this.query.trim(),{ headers: headers })
			.map(res => res.json())
			.subscribe(
			 data => {
				 //console.log(data);
				 if(data.images.length > 0){
					 this.images = data.images;
					 this.grid = Array(Math.ceil(this.images.length/3));
					 this.showSpinner = false;
					 this.noResults = false;

					 let rowNum = 0; //counter to iterate over the rows in the grid

					  for (let i = 0; i < this.images.length; i+=3) { //iterate images
							var n_elements = this.images.length - i >= 3 ? 3 : this.images.length - i;
					    this.grid[rowNum] = Array(n_elements); //declare two elements per row
							for(let j = 0; j < n_elements; j++){
								if (this.images[i+j]) { //check file URI exists
								 this.grid[rowNum][j] = this.images[i+j]; //insert image
							 }
							}

					    rowNum++; //go on to the next row
					  }
				 }else{
					 this.noResults = true;
					 this.showSpinner = false;
					 this.images = [];
				 }

			 },
			 err => {
				 console.log("ERROR!: ", err);
				 this.showImagesPanel = false;

			 });
	}

}



