import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Platform } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { ConceptModalPage } from '../concept-modal/concept-modal'
import { RootModalPage } from '../root-modal/root-modal'

import 'rxjs/add/operator/map';

import * as d3 from 'd3';

export type DataType = {x: any, y: any};

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

	ROOT_NODE_RADIUS = 400;

	margin = {top: 12, right: 12, bottom: 12, left: 12};
	width: number;
  height: number;

  concept: string;

	colors:Array<any>;
	color: any;

	categories:Array<any>;

  svg: any;
  g: any;
	zoom: any;

  simulation: any;
  graph = {
    "nodes": [],
    "links": [],
  };

  node:any;
  link:any;
  text:any;

  cnt:number;
  group: number;

	isStarted: boolean;
	self: any;

  constructor(public navCtrl: NavController,
              public platform: Platform,
              public modalCtrl: ModalController) {

		this.isStarted = false;
    this.concept = "";
    this.cnt = 1;
    this.group = 1;

		this.colors = [
			{color:'red', hex:'#f44336'},
			{color:'pink', hex:'#e91e63'},
			{color:'purple', hex:'#9c27b0'},
		  {color:'indigo', hex:'#3f51b5'},
			{color:'blue', hex:'#2196f3'},
			{color:'cyan', hex:'#00bcd4'},
			{color:'teal', hex:'#009688'},
			{color:'green', hex:'#4caf50'},
			{color:'yellow', hex:'#ffeb3b'},
			{color:'orange', hex:'#ff9800'},
			{color:'brown', hex:'#795548'},
			{color:'grey', hex:'#9e9e9e'},
			{color:'blue-grey', hex:'#607d8b'},
			{color:'amber', hex:'#ffc107'},
			{color:'lime', hex:'#cddc39'},
			{color:'black', hex:'#000000'}
		];

		this.categories = [];

    this.zoom = d3.zoom()
			.scaleExtent([0.25, 4])
			.on("zoom", this.zoomed.bind(this));

  }

  ionViewDidLoad() {
    this.platform.ready().then((readySource) => {
      this.width = this.platform.width() - this.margin.left - this.margin.right ;
      this.height = this.platform.height() - this.margin.top - this.margin.bottom;
      this.initSvg();
    });
  }

	startWizard ( ){
		var modal = this.modalCtrl
			.create(RootModalPage, { concept: this.concept })

		modal.onDidDismiss(data => {
			//console.log(data);
      if(data === null) return;

			console.log(data.concept)
			this.concept = data.concept;
			this.categories.push({name: 'main concept', color:data.color});
      this.startWeb( data );
    });

		modal.present();
	}


	reset( ev ){
		this.concept = "";
		this.cnt = 1;
		this.group = 1;
		this.zoom = d3.zoom()
			.scaleExtent([0.25, 4])
			.on("zoom", this.zoomed.bind(this));
		this.svg.selectAll("*").remove();
		this.isStarted = false
	}

	cloudShape(  ){
		//return "M 45 200 A 40 40 0 0 1 40 120 A 30 30 0 1 1 115 105 C 155 90 160 130 160 130 A 30 30 0 1 1 165 200 Z";
    //return "M 90 400 A 80 80 0 0 1 80 240 A 60 60 0 1 1 230 210 C 310 180 320 260 320 260 A 60 60 0 1 1 330 400 Z";
		return "M 0 " + this.ROOT_NODE_RADIUS + " V 0 L " + this.ROOT_NODE_RADIUS + " 0 V " + this.ROOT_NODE_RADIUS + " z";
  }

	wrap(text, width) {
	  text.each(function() {
			//console.log(d3.select(this).text());

					var text = d3.select(this),
							y = text.attr("y"),
				 			dy = parseFloat(text.attr("dy")),
							lineHeight = 1.1,
							lineNumber = 0,
							lines = text.text().split('\n');
							console.log(lines);
							text.text(null);
							var tspan;
							if(lines.length === 1){
								tspan = text.append("tspan").attr("x", 0).attr("y", y).attr("dy", dy + "em").text(lines[0]);
							}	else{
								for(let i = 0; i < lines.length; i++){
									 tspan = text.append("tspan").attr("x", 0).attr("y", y).attr("dy", lineNumber++ * lineHeight + dy + "em").text(lines[i]);
								}
							}


	  /*  var text = d3.select(this),
	        words = text.text().split(/\s+/).reverse(),
	        word,
	        line = [],
	        lineNumber = 0,
	        lineHeight = 1.1, // ems
	        y = text.attr("y"),
	        dy = parseFloat(text.attr("dy")),
	        tspan = text.text(null).append("tspan").attr("x", 0).attr("y", y).attr("dy", dy + "em");


	    while (word = words.pop()) {
	      line.push(word);
	      tspan.text(line.join(" "));
	      if ((<SVGTSpanElement>tspan.node()).getComputedTextLength() > width) {
	        line.pop();
	        tspan.text(line.join(" "));
	        line = [word];
	        tspan = text.append("tspan").attr("x", 0).attr("y", y).attr("dy", ++lineNumber * lineHeight + dy + "em").text(word);
	      }
	    }*/

			//console.log(this.getBBox());
  	});
	}

	insertBBox(text, radius, self){
		console.log(self);
		text.each(function(d) {
			d.bb = this.getBBox();
			d3.selectAll('rect').exit().remove();
			d3.selectAll('.labels')
				.insert('rect', 'text')
				.attr('x', 0)
				.attr('y', 0)
				.attr('width', () => { return this.getBBox().width })
				.attr('height', () => { return this.getBBox().height })
				.on('mouseover', self.mouseover.bind(this))
				.on('mouseout', self.mouseout.bind(this));
			});
		}



  startWeb( data ){
		var self = this;
    if(this.concept.trim() === "") return;

		this.isStarted = true;

    this.svg.selectAll("*").remove();
    this.svg.append('svg:defs')
      .append("svg:pattern")
        .attr("id", "fill-0")
        .attr("width", this.ROOT_NODE_RADIUS * 2)
        .attr("height", this.ROOT_NODE_RADIUS * 2)
        .attr("patternUnits", "userSpaceOnUse")
        .append("svg:image")
				.attr("x", 0)
        .attr("y", this.ROOT_NODE_RADIUS / 2)
        .attr("xlink:href", data.img_url)
        .attr("width", this.ROOT_NODE_RADIUS * 2)
        .attr("height", this.ROOT_NODE_RADIUS * 2)
				.attr("preserveAspectRatio", "xMidYMid slice");

    this.graph.nodes = [];
    this.graph.links = [];

    this.simulation = d3.forceSimulation()
        .force("link", d3.forceLink().id(function(d:any) { return d.id; }).distance( (d:any) => {return this.ROOT_NODE_RADIUS + (Math.max(d.source.bb.height, d.target.bb.height) / 2);}).strength(1))
				//.force("link", d3.forceLink().id((d:any) => { return d.id; }).distance( (d:any) => { console.log(this.ROOT_NODE_RADIUS + (d.source.bb.height / 2) + (d.target.bb.height / 2)); return this.ROOT_NODE_RADIUS + (d.source.bb.height / 2) + (d.target.bb.height / 2)}).strength(1))

				.force("charge", d3.forceManyBody())
        //.force("collision", d3.forceCollide(this.ROOT_NODE_RADIUS))
        //.force("center", d3.forceCenter((this.width / 2) + this.ROOT_NODE_RADIUS / 2, (this.height / 2) + this.ROOT_NODE_RADIUS / 2));

    this.graph.nodes[0] = {
      "id": 0,
      "label": this.concept,
      "img_url": data.img_url,
      "group": 1,
      "fx": this.width / 2,
      "fy": this.height / 2,
			"x": this.width / 2,
			"y": this.width / 2,
      "color": data.color
    };


		this.link = this.svg.append("g")
			.attr("class", "links")
			.selectAll("line")
			.data(this.graph.links)
			.enter().append("line");

		this.node = this.svg.append("g")
      .attr("class", "nodes")
      .selectAll("g")
      .data(this.graph.nodes)
      .enter().append("svg:path")
				.style("stroke", () => { return this.colors.find(x => x.color === data.color).hex })
        //.call(d3.drag()
        	// .on("start", this.dragstarted.bind(this))
        	// .on("drag", this.dragged.bind(this))
        	// .on("end", this.dragended.bind(this)));
      //  .on('click', this.clicked.bind(this));

    this.text = this.svg.append("g")
			.attr("class", "labels")
			.selectAll("g")
      .data(this.graph.nodes)
      .enter().append("text")
      	.attr("y", 0)
				.attr("dy", 0)
				.attr("x", 0)
				.style("fill", () => { return this.colors.find((x) => x.color === data.color).hex })
      	.text(function(d) { return d.label; })
				.call(this.wrap, this.ROOT_NODE_RADIUS)
				.call(this.insertBBox, this.ROOT_NODE_RADIUS, self)
				.on('mouseover', this.mouseover.bind(this))
				.on('mouseout', this.mouseout.bind(this))
      	.on('click', this.clicked.bind(this));

				//.on('mouseover', this.mouseover.bind(this))
				//.on('mouseout', this.mouseout.bind(this));

    this.simulation
        .nodes(this.graph.nodes)
        .on("tick", this.ticked.bind(this));

    this.simulation.force("link")
        .links(this.graph.links);
  }


	mouseover( text ){
		console.log('mouseover', text);
		d3.selectAll('text')
			.style("opacity", (d:any) => { return d.label === text.label ?  1 : .3;});

		d3.selectAll('line')
			.style("opacity", .3);

	}

	mouseout( text ) {
		d3.selectAll('text')
			.style("opacity", 1);

		d3.selectAll('line')
			.style("opacity", 1);

		}

  dragstarted( d ){
    d3.event.sourceEvent.stopPropagation();
    if(d.id === 0) return;
    if (!d3.event.active) this.simulation.alphaTarget(0.3).restart();
    d.fx = d.x;
    d.fy = d.y;
  }

  dragged( d ){
    if(d.id === 0) return;
      d.fx = d3.event.x;
      d.fy = d3.event.y;
  }

  dragended( d ){
    d3.event.sourceEvent.stopPropagation();
    if(d.id === 0) return;

    if (!d3.event.active) this.simulation.alphaTarget(0);
    d.fx = null;
    d.fy = null;
  }

  ticked( ){
		this.link
				//.attr("x1", (d) => { return d.source.x - (this.ROOT_NODE_RADIUS / 2); })
				.attr("x1", (d) => { return d.source.x })
				//.attr("y1", (d) => { return d.source.y; })
				.attr("y1", (d) =>{ return d.source.y + d.source.bb.height / 2})
				.attr("x2", (d) =>{ return d.target.x } )
				//.attr("x2", (d) => { return this.ROOT_NODE_RADIUS / 2 })
				.attr("y2", (d) =>{ return d.target.y + d.target.bb.height / 2});

		this.node
				.attr("transform", (d) => {
					return "translate(" + (d.x) + ","
					+ (d.y) + ")";
					});

		this.text
       .attr("transform", (d) =>{
				  return "translate(" + (d.x) + ","
					+ (d.y ) + ")";
			});

			this.svg.selectAll('rect').data(this.graph.nodes)
				.attr("transform", (d) =>{
					 return "translate(" + (d.x  - d.bb.width / 2) + ","
					 + (d.y) + ")";
		});


  }

  clicked ( d ){
    if (d3.event.defaultPrevented) return;

		var modal = this.modalCtrl
			.create(ConceptModalPage, { parentConcept: this.concept })

		modal.onDidDismiss(data => {
			if(data == null) return;
			if(d.group === 1) this.group++;


			var n = {	id: ""+ this.cnt++,
								group: d.group === 1 ? this.group : d.group,
								label: data.label,
								color: data.color,
								img_url: data.img_url }
			this.graph.nodes.push(n);
			this.graph.links.push({source: d.id, target: n.id, group: n.group, color:n.color});

			this.svg.select("defs")
			.append("svg:pattern")
			.attr("id", "fill-"+n.id)
			.attr("width", this.ROOT_NODE_RADIUS * 2)
			.attr("height", this.ROOT_NODE_RADIUS * 2)
			.attr("patternUnits", "userSpaceOnUse")
			.append("svg:image")
			.attr("x", 0)
			.attr("y", this.ROOT_NODE_RADIUS / 2)
			.attr("xlink:href", n.img_url)
			.attr("width", this.ROOT_NODE_RADIUS * 2)
			.attr("height", this.ROOT_NODE_RADIUS * 2)
			.attr("preserveAspectRatio", "xMidYMid slice");

			this.restart();
	   });

		modal.present();

  }

 flatten(root) {
  	var nodes = [], i = 0;

	  function recurse(node) {
	    if (node.children) node.children.forEach(recurse);
	    if (!node.id) node.id = ++i;
	    nodes.push(node);
	  }

	  recurse(root);
	  return nodes;
	}


  restart(){
    var self = this;


    this.node = this.node.data(this.graph.nodes, function(d) { return d.id;});
    this.node.exit().remove();
    this.node = this.node.enter()
      .append("svg:path")
				//.attr("d", (d) => { return this.cloudShape();})
			  .attr("y",0)
				.attr("x",0)
		        .call(d3.drag()
            .on("start", this.dragstarted.bind(this))
            .on("drag", this.dragged.bind(this))
            .on("end", this.dragended.bind(this)));
        //  .on('click', this.clicked.bind(this));

			this.text = this.text.data(this.graph.nodes, d => {return d.label});
			this.text.exit().remove();

			this.text = this.text.enter()
				.append("text")
				.attr("y", 0)
				.attr("dy", 0)
				.attr("x", 0)
				.style("fill", (d) => { return this.colors.find((x) => x.color === d.color).hex })
				.text(function(d) { return d.label; })

				.call(this.wrap, this.ROOT_NODE_RADIUS)
				.call(this.insertBBox, this.ROOT_NODE_RADIUS, self)
				.call(d3.drag()
        	.on("start", this.dragstarted.bind(this))
        	.on("drag", this.dragged.bind(this))
        	.on("end", this.dragended.bind(this)))


				.on('click', this.clicked.bind(this))
				.on('mouseover', this.mouseover.bind(this))
				.on('mouseout', this.mouseout.bind(this))
				.merge(this.text);


		this.link = this.link.data(this.graph.links, function(d) {return d.source.id + "-" + d.target.id; });
		this.link.exit().remove();
		this.link = this.link.enter()
			.append("line")
				.style("stroke", (d) => { return self.colors.find(x => x.color === d.color).hex })
				.merge(this.link);
    // Update and restart the simulation.
    this.simulation.nodes(this.graph.nodes);
    this.simulation.force("link").links(this.graph.links);
    this.simulation.alpha(1).restart();

  }


  initSvg(){
    this.color = d3.scaleOrdinal(d3.schemeCategory20);
    this.svg = d3.select("#concept-web")
			.append("svg")
    	.attr("width",  this.width)
    	.attr("height", this.height);

		this.svg.call(this.zoom);
  }

	onResize( ev ){
		this.width = this.platform.width() - this.margin.left - this.margin.right ;
		this.height = this.platform.height() - this.margin.top - this.margin.bottom;
	}

	zoomed( ){
		this.svg.selectAll('g').attr('transform', d3.event.transform);
	}

	zoomIn( ev ){
		this.transition(1.25);
	}

	zoomOut( ev ){
		this.transition(0.75);
	}

	transition(zoomLevel) {
	  this.svg.transition()
	      .delay(50)
	      .duration(400)
	      .call(this.zoom.scaleBy, zoomLevel);
	}

}



